	module.exports = {"data":{"post":{"html":"<p>Unlike last year, when we celebrated the millenium at Harburg General Hospital (when Mama was suffering from fever and was being cooled by cabbage leaves), we're celebrating this millenium at home. Both Mama and Christopher are recovering from colds, so we turned down an offer to go sledding with Christopher's uncle. </p>\n<p>We also won't be going to the fireworks at the harbor in Hamburg. That will be cold, crowded, and with kids throwing firecrackers around, more than a little dangerous. It seems that this time of year normally safety-conscious Germans take leave of their senses. First they put lit candles in their Christmas trees, then they throw firecrackers at one another on New Years Eve. Or fall down drunk into the River Elbe.</p>\n<p>Speaking of safety, Christopher is at this moment happily pulling up the little rubber anti-slip mats in the shower. Don't ask why...</p>\n<p>You don't believe the part about the cabbage leaves last year? Here's proof...</p>\n<p><a href=\"http://albums.photopoint.com/j/ViewPhoto?u=185392&a=1369040&p=14798052\"><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2000/12/cabbagepatch.jpg\" height=\"225\" width=\"300\" border=\"0\" alt=\"cabbagepatch.jpg: \" /></a></p>","fields":{"title":"End of everything","date":"31 Dec 2000","url":"https://papascott.de/2000-12-31-end-of-everything/"}}},"pathContext":{"slug":"/2000-12-31-end-of-everything/","prev":{"fields":{"title":"White stuff","date":"30 Dec 2000","slug":"/2000-12-30-white-stuff/"}},"next":{"fields":{"title":"Happy New Year","date":"01 Jan 2001","slug":"/2001-01-01-happy-new-year/"}}}}

